import React from "react";

const CoinRow = ({ coin, index }) => {
  return (
      <tr>
        <td>{index}</td>
        <td>
        <img src={coin.image} alt="coin.name" style={{width: '20px'}} className="img-fluid me-4"/>
        <span>{coin.name}</span>
        <span className="ms-3 text-muted text-uppercase">{coin.symbol}</span>
        </td>
        <td className="text-muted">{coin.current_price.toLocaleString('es')} US$</td>
        <td className={coin.price_change_percentage_24h > 0 ? 'text-success' : 'text-danger' }>{coin.price_change_24h.toLocaleString('es')} US$</td>
        <td className="text-muted">{coin.total_volume.toLocaleString('es')} US$</td>
        <td className="text-muted">{coin.market_cap.toLocaleString('es')} US$</td>
        <td className="text-muted">{coin.ath.toLocaleString('es')} US$</td>




      </tr>
  );
};

export default CoinRow;
